import React from "react";

import { ReactComponent as Plus } from "../../common/svg/Plus.svg";
import { ReactComponent as Minus } from "../../common/svg/Minus.svg";
import { getClasses } from "../../utils";
import { Buttons } from "./styles";

const CartButtons = ({
  id,
  extraStyling = {},
  price,
  addIsDisabled = false,
  addToCart,
  removeFromCart,
  removeIsDisabled = false,
}) => {
  const btnClickHandler = (e, type) => {
    e.preventDefault();
    const action = type === "addToCart" ? addToCart : removeFromCart;
    action(id, price);
  };
  console.log("cart btns render");
  const addBtnClasses = getClasses({
    ["btn"]: true,
    disabled: addIsDisabled,
  });
  const removeBtnClasses = getClasses({
    ["btn"]: true,
    disabled: removeIsDisabled,
  });
  return (
    <Buttons style={{ ...extraStyling }} title = 'Plus (Add) button coresponded to the product amount limitation'>
      {!addIsDisabled && (
        <Plus
          className={addBtnClasses}
          onClick={(e) => btnClickHandler(e, "addToCart")}
        />
      )}
      <Minus
        className={removeBtnClasses}
        onClick={(e) => btnClickHandler(e, "removeFromCart")}
      />
    </Buttons>
  );
};

export default React.memo(CartButtons);
