import styled from "styled-components";
export const Main = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  min-width: 845px;
  @media (max-width: 600px) {
    padding: 0 2px;
    min-width: auto;
  }
  .container{
    display:flex;
    width:100% ;
    justify-content: space-between;
    align-items:center ;
  p {
    padding: 15px 0;
  }
}
`;
export const Product = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  .top {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    @media (max-width: 600px) {
      flex-direction: column;
    }
    .left {
      img {
        user-select:none ;
        width: 310px;
        height: auto;
        @media (max-width: 600px) {
          width: 100%;
          position:sticky ;
        }
      }
    }
    .right {
      padding: 0 15px;
      width: 50%;
      line-height: 30px;
      text-align: left;
      @media (max-width: 600px) {
        padding: 0 2px;
        width: 100%;
      }
    }
  }
`;

export const Buttons = styled.div`
  display: flex;
  align-self: flex-end;
`;
