import styled from "styled-components";
import { disableStyle } from "../../common/style";

export const Buttons = styled.div`
  border: solid var(--borderColor) 1px;
  align-self: flex-end;
  border-radius: 4px;
  padding: 10px;
  width: 50%;
  display: flex;
  justify-content: space-around;
  
  @media (max-width: 600px) {
    width: 100%;
    border-radius: 5000px;
    padding: 5px;
    border: none;
  }
  .btn {
    width: 25px;
    height: 25px;
    border: solid 1px var(--borderColor);
    border-radius: 50000px;
    padding: 5px;
    @media (max-width: 600px) {
      width: 50px;
      height: 50px;

      border-radius: 5000px;
      padding: 5px;

   
    }
    &.disabled{
     ${disableStyle}
      }
    cursor: pointer;

  }
`;
