import React, { useCallback, useEffect, useState } from "react";
import { useParams, useNavigate } from "react-router-dom";
import { getProductById } from "../../api/products";
import { Button } from "../../common/style";
import { useShoppingCartStore } from "../../store/shoopingCartStore";
import { Product, Main } from "./ProductPage.style";
import CartButtons from "./../../components/CartButtons.jsx/CartButtons";
import { LoaderWrapper } from "../../common/style";
import CircleLoader from "../../components/CircleLoader/CircleLoader";
import { useCurrencyStore } from "../../store/currencyStore";
import { getOccurrence } from "../../utils";

const ProductPage = () => {
  const [current, setCurrent] = useState();
  const [isAvailible, setIsAvilible] = useState(true);
  const { currency } = useCurrencyStore();
  const navigate = useNavigate();
  const { id } = useParams();

  //fucc for cart buttons
  const { removeFromCart, addToCart, sc } = useShoppingCartStore();
  const addMemoized = useCallback(addToCart, []);
  const removeMemoized = useCallback(removeFromCart, []);

  useEffect(() => {
    async function fetchData() {
      const res = await getProductById(id);
      setCurrent(res);
      setIsAvilible(res.qtt > 0)
    }
    fetchData();
  }, [id]);

  const goBackHandler = () => {
    navigate(-1);
  };
  const amount = getOccurrence(sc,id)

  return (
    <Main>
      <div className="container">
        <p className="title">{current?.title}</p>
        <Button
          extraStyling={{ padding: "5px", width: "fit-content" }}
          onClick={goBackHandler}
        >
          Back to gallery
        </Button>
      </div>
      {!current ? (
        <LoaderWrapper>
          <CircleLoader />
        </LoaderWrapper>
      ) : (
        <Product>
          <div className="top">
            <div className="left">
              <img src={current?.src} alt = {current.title} />
            { isAvailible && <CartButtons
                id={current.id}
                addIsDisabled = {amount >= current.qtt}
                price={current.price}
                addToCart={addMemoized}
                removeFromCart={removeMemoized}
                removeIsDisabled = {amount< 1}
                
              />}
            </div>
            <div className="right">
              <div>{current?.fullDetails}</div>
              <div>
                {currency.symbol}
                {current?.price}
              </div>
              <div>
                Cillum ullamco ullamco fugiat tempor fugiat nulla labore dolore
                id aute deserunt velit. Minim et ex sunt esse elit dolore
                adipisicing. Lorem dolor consectetur et sit nisi magna nisi elit
                qui fugiat adipisicing.
              </div>
            </div>
          </div>
        </Product>
      )}
    </Main>
  );
};

export default ProductPage;
